import clsx from 'clsx';
import { useState } from "react";

import { ModalType } from './types';

import { registration as registrationApi, Registration as RegistrationData } from '../../services/api'

import styles from './ModalRegistration.module.scss';

function ModalRegistration (props: ModalType) {

	const Arrurrency = [
		{
			name: ' ',
			code: 'RUB',
		},
		{
			name: ' ',
			code: 'USD',
		},
		{
			name: '',
			code: 'EUR',
		},
	];

	const [isurrencyOpen, setIsurrencyOpen] = useState(false);
	const [isurrency, setIsurrency] = useState(0);
	const [isApprove, setIsApprove] = useState(false);

	const [email, setEmail] = useState('');
	const [phone, setPhone] = useState('');

	function handleEmail(event: any) {
		setEmail(event.target.value);
	}

	function handlePhone(event: any) {
		setPhone(event.target.value);
	}

	const toggeSelect = () => {
		setIsurrencyOpen(current => !current);
	}

	function Updateurrency(index: number) {
		setIsurrency(index);
		toggeSelect();
	}

	const [isTab, setIsTab] = useState('email');

	return (
		<div className={styles.ModalRegistration}>
			<h2 className={styles.title}></h2>
			<div className={styles.btnWrapper}>
				<button 
					onClick={() => setIsTab('email')}
					className={clsx(
						styles.tabBtn,
						{[styles.tabBtnActive] : isTab === 'email' }
					)}
				></button>
				<button 
					onClick={() => setIsTab('phone')}
					className={clsx(
						styles.tabBtn,
						{[styles.tabBtnActive] : isTab === 'phone' }
					)}
				></button>
			</div>
			<div className={clsx(
				styles.tab,
				{[styles.tabActive] : isTab === 'email' }
			)}>
				<label className={styles.label}>
					<div className={styles.labelInfo}></div>
					<input className={styles.labelInput} type='email' value={email} onChange={handleEmail} placeholder='test@yandex.ru'/>
				</label>
			</div>
			<div className={clsx(
				styles.tab,
				{[styles.tabActive] : isTab === 'phone' }
			)}>
				<div className={styles.label}>
					<div className={styles.labelInfo}> </div>
					<div className={styles.inputWrapper}>
						<div className={styles.selectCode}>+7</div>
						<input className={styles.labelInputPhone} type='text' value={phone} onChange={handlePhone} placeholder='9500461487'/>
					</div>
				</div>
			</div>
			<div className={styles.text}></div>
			<div className={styles.selecturrencyWrapper}>
				<div 
					onClick={toggeSelect}
					className={clsx(
					styles.selecturrency,
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'RUB'},
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'USD'},
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'EUR'}
				)}>
					{Arrurrency[isurrency].code} ({Arrurrency[isurrency].name})
				</div>
				<ul className={clsx(
					styles.selecturrencyList,
					{[styles.selecturrencyListOpen] : isurrencyOpen}
				)}>
					{Arrurrency.map((item, index) => (
						<li 
							key={index} 
							className={clsx(
								styles.selecturrencyItem,
								{[styles.selecturrencyItemRu] : item.code === 'RUB'},
								{[styles.selecturrencyItemRu] : item.code === 'USD'},
								{[styles.selecturrencyItemRu] : item.code === 'EUR'},
							)}
							onClick={() => { Updateurrency(index)}}>
							{item.code} ({item.name})
						</li>
					))}
				</ul>
			</div>
			<label className={clsx(
					styles.approveLabel,
					{ [styles.approveLabelActive] : isApprove},
				)}>
				<input className={styles.approveCheckbox} type="checkbox" checked={isApprove} onChange={() => setIsApprove((approve) => !approve)} />       
			</label>
			<button className={clsx(
				styles.btn,
				{ [styles.btnActive] : isApprove},
			)} onClick={async () => {
				const data = {
					Email: email,
					Phone: phone,
					Currency: Arrurrency[isurrency].code
				}
				const reply = await registrationApi(data as RegistrationData);
				console.log(reply);
			}}></button>
		</div>
	);
}

export default ModalRegistration;
