import clsx from 'clsx';
import { useState } from "react";

import { ModalType } from './types';

import styles from './ModalRegistrationFull.module.scss';

function ModalRegistrationFull (props: ModalType) {

	const Arrurrency = [
		{
			name: ' ',
			code: 'RUB',
		},
		{
			name: ' ',
			code: 'USD',
		},
		{
			name: '',
			code: 'EUR',
		},
	];

	const Arrountry = [
		{
			name: '',
			code: 'ru',
		},
		{
			name: '',
			code: 'pl',
		},
		{
			name: '',
			code: 'de',
		},
	];

	const [isountryOpen, setIsountryOpen] = useState(false);
	const [isountry, setIsountry] = useState(0);

	const [isurrencyOpen, setIsurrencyOpen] = useState(false);
	const [isurrency, setIsurrency] = useState(0);

	const [firstname, setFirstname] = useState('');
	const [lastname, setLastname] = useState('');
	const [email, setEmail] = useState('');
	const [phone, setPhone] = useState('');
	const [promocode, setPromocode] = useState('');
	const [date, setDate] = useState('');
	const [address, setAddress] = useState('');

	function handleFirstname(event: any) {
		setFirstname(event.target.value);
	}

	function handleLastname(event: any) {
		setLastname(event.target.value);
	}

	function handleEmail(event: any) {
		setEmail(event.target.value);
	}

	function handlePhone(event: any) {
		setPhone(event.target.value);
	}

	const handlePromocode = (e: any) => {
		const regex = /^[0-9\b]+$/;
		if (e.target.value === "" || regex.test(e.target.value)) {
			setPromocode(e.target.value);
		}
	};

	function handleDate(event: any) {
		setDate(event.target.value);
	}

	const toggeSelectountry = () => {
		setIsountryOpen(current => !current);
	}

	function handleAddress(event: any) {
		setAddress(event.target.value);
	}

	function Updateountry(index: number) {
		setIsountry(index);
		toggeSelectountry();
	}

	const toggeSelect = () => {
		setIsurrencyOpen(current => !current);
	}

	function Updateurrency(index: number) {
		setIsurrency(index);
		toggeSelect();
	}

	return (
		<div className={styles.ModalRegistrationFull}>
			<h2 className={styles.title}></h2>
			<label className={styles.label}>
				<div className={styles.labelInfo}></div>
				<input className={styles.labelInput} type='text' value={firstname} onChange={handleFirstname} placeholder=' '/>
			</label>
			<label className={styles.label}>
				<div className={styles.labelInfo}></div>
				<input className={styles.labelInput} type='text' value={lastname} onChange={handleLastname} placeholder=' '/>
			</label>
			<label className={styles.label}>
				<div className={styles.labelInfo}></div>
				<input className={styles.labelInput} type='email' value={email} onChange={handleEmail} placeholder='test@yandex.ru'/>
			</label>
			<div className={styles.label}>
				<div className={styles.labelInfo}> </div>
				<input className={styles.labelInput} type='text' value={phone} onChange={handlePhone} placeholder='+7XXXXXXXXXX'/>
			</div>
			<div className={clsx(styles.label, styles.labelCode)}>
				<div className={styles.labelInfo}> </div>
				<div className={styles.codeWrapper}>
					<input className={styles.labelInput} type='text' value={promocode} onChange={handlePromocode} placeholder='______' maxLength={6}/>
				</div>
			</div>
			<button className={styles.btn}> </button>
			<div className={styles.btnText}>   0 </div>
			<label className={styles.label}>
				<div className={styles.labelInfo}> </div>
				<input className={styles.labelInput} type='text' value={date} onChange={handleDate} placeholder='  '/>
			</label>
			<div className={styles.text}> </div>
			<div className={styles.selectountryWrapper}>
				<div 
					onClick={toggeSelectountry}
					className={clsx(
					styles.selectountry,
				)}>
					{Arrountry[isountry].name}
				</div>
				<ul className={clsx(
					styles.selectountryList,
					{[styles.selectountryListOpen] : isountryOpen}
				)}>
					{Arrountry.map((item, index) => (
						<li 
							key={index} 
							className={clsx(styles.selectountryItem)}
							onClick={() => { Updateountry(index)}}
						>
							{item.name}
						</li>
					))}
				</ul>
			</div>
			<label className={styles.label}>
				<div className={styles.labelInfo}></div>
				<input className={styles.labelInput} type='text' value={address} onChange={handleAddress} placeholder=' '/>
			</label>
			<div className={styles.text}></div>
			<div className={styles.selecturrencyWrapper}>
				<div 
					onClick={toggeSelect}
					className={clsx(
					styles.selecturrency,
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'RUB'},
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'USD'},
					{[styles.selecturrencyRu] : Arrurrency[isurrency].code === 'EUR'}
				)}>
					{Arrurrency[isurrency].code} ({Arrurrency[isurrency].name})
				</div>
				<ul className={clsx(
					styles.selecturrencyList,
					{[styles.selecturrencyListOpen] : isurrencyOpen}
				)}>
					{Arrurrency.map((item, index) => (
						<li 
							key={index} 
							className={clsx(
								styles.selecturrencyItem,
								{[styles.selecturrencyItemRu] : item.code === 'RUB'},
								{[styles.selecturrencyItemRu] : item.code === 'USD'},
								{[styles.selecturrencyItemRu] : item.code === 'EUR'},
							)}
							onClick={() => { Updateurrency(index)}}>
							{item.code} ({item.name})
						</li>
					))}
				</ul>
			</div>
		</div>
	);
}

export default ModalRegistrationFull;
