import { ModalType } from './types';
import clsx from 'clsx';
import { useState } from "react";

import styles from './ModalRefill.module.scss';

function ModalRefill (props: ModalType) {

	const [isMethodOpen, setIsMethodOpen] = useState(false);
	const [isMethod, setIsMethod] = useState(0);

	const toggeSelectMethod = () => {
		setIsMethodOpen(current => !current);
	}

	function UpdateMethod(index: number) {
		setIsMethod(index);
		toggeSelectMethod();
	}

	const ArrMethod = [
		{ name: 'VISA' },
		{ name: 'MASTERCARD' },
		{ name: '' },
		{ name: 'American Express' },
		{ name: 'UnionPay' },
		{ name: 'QIWI KZ' },
		{ name: '' },
		{ name: 'WebMoney' },
		{ name: ' ' },
		{ name: ' QIWI' },
		{ name: 'Apple Pay' },
		{ name: 'Google Pay' },
		{ name: 'Capitalist' },
		{ name: 'Samsung Pay' },
		{ name: 'PayPal' },
		{ name: 'PAYEER' },
		{ name: 'MuchBetter' },
	];

	const [isurrencyOpen, setIsurrencyOpen] = useState(false);
	const [isurrency, setIsurrency] = useState(0);

	const toggeSelecturrency = () => {
		setIsurrencyOpen(current => !current);
	}

	function Updateurrency(index: number) {
		setIsurrency(index);
		toggeSelecturrency();
	}

	const Arrurrency = [
		{ name: '' },
		{ name: '$' },
		{ name: '' },
		{ name: '' },
		{ name: '' },
		{ name: '' },
	];

	const [sum, setSum] = useState("");

	const updateSum = (e: any) => {
		const regex = /^[\D0]+|\D/;
		if (e.target.value === "" || !regex.test(e.target.value)) {
			setSum(e.target.value);
		}
	}

	return (
		<div className={styles.modalRefill}>
			<h2 className={styles.title}> </h2>
			<div className={styles.text}> </div>
			<div className={styles.selectMethodWrapper}>
				<div 
					onClick={toggeSelectMethod}
					className={clsx(
					styles.selectMethod,
				)}>
					{ArrMethod[isMethod].name}
				</div>
				<ul className={clsx(
					styles.selectMethodList,
					{[styles.selectMethodListOpen] : isMethodOpen}
				)}>
					{ArrMethod.map((item, index) => (
						<li 
							key={index} 
							className={clsx(styles.selectMethodItem)}
							onClick={() => { UpdateMethod(index)}}
						>
							{item.name}
						</li>
					))}
				</ul>
			</div>
			<div className={styles.text}> </div>
			<div className={styles.selecturrencyBigWrapper}>
				<div className={styles.selecturrencyWrapper}>
					<div 
						onClick={toggeSelecturrency}
						className={clsx(
						styles.selecturrency,
					)}>
						{Arrurrency[isurrency].name}
					</div>
					<ul className={clsx(
						styles.selecturrencyList,
						{[styles.selecturrencyListOpen] : isurrencyOpen}
					)}>
						{Arrurrency.map((item, index) => (
							<li 
								key={index} 
								className={clsx(styles.selecturrencyItem)}
								onClick={() => { Updateurrency(index)}}
							>
								{item.name}
							</li>
						))}
					</ul>
				</div>
				<input className={styles.itemInput} type="text" placeholder=" " value={sum} onChange={updateSum}/>
			</div>
			<button className={styles.btn}> </button>
		</div>
	);
}

export default ModalRefill;
